@import './../../../n1-main/m1-ui/common/components/styles/variables.scss';
@import './../../../n1-main/m1-ui/common/components/styles/mixins.scss';

.tableWrap {
   max-height: 440px;
   overflow-y: auto;
   overflow-x: hidden;
   margin-bottom: 24px;
   @include font(13px, 400, 120%, #000000);
   box-shadow: 0px 4px 14px rgba(45, 46, 70, 0.15)
}



.tableWrap {
   max-height: 440px;
   overflow-y: auto;
   overflow-x: hidden;
   margin-bottom: 24px;

   table {
      table-layout: fixed;
      width: 100%;
      border: none;
      border-collapse: collapse;
   }

   thead th:nth-child(1) {
      width: 185px;
   }

   thead th:nth-child(2) {
      width: 77px;
   }

   thead th:nth-child(3) {
      width: 135px;
   }

   thead th:nth-child(4) {
      width: 112px;
   }

   thead th:nth-child(5) {
      width: 160px;
   }

   th {
      background-color: #ECECF9;
   }

   tbody tr:nth-child(even) {
      background: #F5F4FA;
   }

   th {
      height: 48px;
      padding-right: 10px;
      text-align: left;
   }

   td {
      height: 48px;
      padding-right: 10px;
   }

   th:first-child {
      padding-left: 24px;
   }
}

.btn {
   background-color: transparent;
   border: none;
   padding: 3px;
   cursor: pointer;
}

// при нажатии на стрелку в графе "Last Updated"
.active {
   transform: rotate(180deg);
}