@import './../../../../n1-main/m1-ui/common/components/styles/variables.scss';
@import './../../../../n1-main/m1-ui/common/components/styles/mixins.scss';

.search {
   position: relative;

   &::after {
      content: "";
      width: 13px;
      height: 13px;
      position: absolute;
      top: 12px;
      left: 17px;
      background-image: url("../../../../assets/img/search-icon.svg");
      opacity: 0.4;
   }
}

.inputSearch {
   height: 36px;
   width: 461px;
   padding-left: 40px;
   background-color: #ECECF9;
   opacity: 0.5;
   border: 1px solid #635D80;
   border-radius: 2px;
   @include font(14px, 500, 170%, $textColor);
}

input::placeholder {
   @include font(14px, 400, 170%, $textColor);
}